*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content{
  height: 100%;
}

.navbar{
  background-color: rgba(0, 0, 0, 0.466) !important;
}

.register-page{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
}

/* transaction page */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
  margin-bottom: 10px;
}

.switch-icons{
  font-size: 20px;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 5px;
}

/* antd icons */
.anticon{
  
  cursor: pointer;
}

.active-icon{
  color: black;
}
.inactive-icon{
  color: gray;
}
.filter-tab .ant-select-selector {
  width: 150px !important;
}